<!--  -->
<template>
  <el-row class="library">
    <el-col :xs="0"
            :sm="6"
            :md="6"
            :lg="6"
            :xl="6"
            class="left hidden-xs-only">
      <el-switch v-model="value"
                 class="show-color"
                 style="display: block"
                 active-color="#ff4949"
                 inactive-color="#818080"
                 inactive-text="show color"
                 @change="changeSwitch">
      </el-switch>
      <ul class="father-wrapper">
        <li v-for="(item,index) in leftList"
            :key="index+'key'"
            class="father"
            :class="{'active':showChild== index}"
            @click="clickShowChild(index,item.father)">
          <span class="title">{{item.father}}</span>
          <ul class="child-wrapper"
              v-show="showChild == index">
            <li v-for="(child,index2) in item.child"
                :key="index2+'child'"
                class="child">
              <span class="child-item"
                    :class="{'active':title === child}"
                    @click.stop="clickChild(child)">
                {{child}}
              </span>
            </li>
          </ul>
        </li>

      </ul>
    </el-col>
    <el-col class="right">
      <el-row>
        <div class="top">
          <h1 class="title">{{title}}</h1>
        </div>
        <div v-loading="loading"
             element-loading-text=""
             element-loading-spinner="el-icon-loading"
             element-loading-background="rgba(255, 255, 255, 0.8)">
          <el-col :xs="12"
                  :sm="24"
                  :md="6"
                  :lg="6"
                  :xl="6"
                  v-for="(item,index) in imgList"
                  :key="index"
                  class="content">
            <el-image lazy
                      :src="require('@/assets/'+color+'/svg/'+item.hexcode+'.svg')"> </el-image>
            <div class="title">{{item.annotation}}</div>
            <div class="desc">{{item.hexcode}}</div>
          </el-col>
        </div>

      </el-row>
    </el-col>
  </el-row>
</template>

<script>
const ERR_OK = 0
export default {
  data () {
    return {
      leftList: [
        {
          father: 'all OpenMojis',
          child: []
        },
        {
          father: 'smileys-emotion',
          child: [
            'face-smiling',
            'face-affection',
            'face-tongue',
            'face-hand',
            'face-neutral-skeptical',
            'face-sleepy',
            'face-unwell',
            'face-hat',
            'face-glasses',
            'face-concerned',
            'face-negative',
            'face-costume',
            'cat-face',
            'monkey-face',
            'emotion'
          ]
        },
        {
          father: 'people-body',
          child: [
            'hand-fingers-open',
            'hand-fingers-partial',
            'hand-single-finger',
            'hand-fingers-closed',
            'hands',
            'hand-prop',
            'body-parts',
            'person',
            'person-gesture',
            'person-role',
            'person-fantasy',
            'person-activity',
            'person-sport',
            'person-resting',
            'family',
            'person-symbol'
          ]
        },
        {
          father: 'component',
          child: [
            'skin-tone',
            'hair-style'
          ]
        }, {
          father: 'animals-nature',
          child: [
            'animal-mammal',
            'animal-bird',
            'animal-amphibian',
            'animal-reptile',
            'animal-marine',
            'animal-bug',
            'plant-flower',
            'plant-other'
          ]
        }, {
          father: 'food-drink',
          child: [
            'food-fruit',
            'food-vegetable',
            'food-prepared',
            'food-asian',
            'food-marine',
            'food-sweet',
            'drink',
            'dishware'
          ]
        }, {
          father: 'travel-places',
          child: [
            'place-map',
            'place-geographic',
            'place-building',
            'place-religious',
            'place-other',
            'transport-ground',
            'transport-water',
            'transport-air',
            'hotel',
            'time',
            'sky-weather'
          ]
        }, {
          father: 'activities',
          child: [
            'event',
            'award-medal',
            'sport',
            'game',
            'arts-crafts'

          ]
        }, {
          father: 'objects',
          child: [
            'clothing',
            'sound',
            'music',
            'musical-instrument',
            'phone',
            'computer',
            'light-video',
            'book-paper',
            'money',
            'mail',
            'writing',
            'office',
            'lock',
            'tool',
            'science',
            'medical',
            'household',
            'other-object'
          ]
        }, {
          father: 'symbols',
          child: [
            'transport-sign',
            'warning',
            'arrow',
            'religion',
            'zodiac',
            'av-symbol',
            'gender',
            'other-symbol',
            'keycap',
            'alphanum',
            'geometric'
          ]
        },
        {
          father: 'flags',
          child: [
            'flag',
            'country-flag',
            'subdivision-flag'
          ]
        }, {
          father: 'extras-openmoji',
          child: [
            'animals-nature',
            'brand',
            'emergency',
            'food-drink',
            'interaction',
            'objects',
            'people',
            'technology',
            'travel-places',
            'ui-element'
          ]
        }, {
          father: 'extras-unicode',
          child: [
            'symbol-other'
          ]
        }

      ],
      imgList: [],
      value: true,
      showChild: 0,
      title: 'all OpenMojis',
      color: 'color',
      loading: true
    }
  },
  created () {
    this.$axios({
      url: '/api/appData',
      methods: 'GET'
    }).then(res => {
      console.info(res)

      if (ERR_OK === res.data.errno) {
        this.imgList = res.data.data
        localStorage.setItem('imgList', JSON.stringify(this.imgList))
        this.loading = false
        console.log(this.imgList)
      }
    }).catch(err => {
      console.info(err)
    })
  },
  components: {},
  computed: {},
  methods: {
    clickShowChild (index, father) {
      this.title = father
      if (index === 0) {
        this.imgList = JSON.parse(localStorage.getItem('imgList'))
        return
      }
      document.getElementsByTagName('body')[0].scrollTop = 0
      console.log(document.getElementsByTagName('body')[0].scrollTop)
      console.log(this.showChild)
      if (this.showChild === index) {
        this.showChild = -1
        return
      }
      this.showChild = index
      let imgList = JSON.parse(localStorage.getItem('imgList'))
      let result = []
      imgList.forEach((img, index) => {
        if (img.group === father) {
          result.push(
            {
              annotation: img.annotation,
              hexcode: img.hexcode
            }
          )
        }
      })
      // subgroups
      console.log(result)
      this.imgList = result
    },
    clickChild (child) {
      this.title = child
      let imgList = JSON.parse(localStorage.getItem('imgList'))
      let result = []
      imgList.forEach((img, index) => {
        if (img.subgroups === child) {
          result.push(
            {
              annotation: img.annotation,
              hexcode: img.hexcode
            }
          )
        }
      })
      // subgroups
      console.log(result)
      this.imgList = result
    },
    changeSwitch () {
      console.log(this.value)
      if (this.value) {
        this.color = 'color'
      } else {
        this.color = 'black'
      }
    }
  }
}

</script>
<style lang='stylus' scoped>
/deep/.el-switch__label.is-active, /deep/.el-switch__label
  color: #242424
/deep/ .el-loading-mask
  // top: 300px
  height: 100vh
.library
  display: flex
  // min-height: 100vh
  // padding-bottom: 10%
  .left
    padding: 5% 0
    z-index: 50
    display: flex
    flex-direction: column
    align-items: center
    z-index: 1
    overflow: auto
    position: fixed
    top: 80px
    height: 80vh
    width: 400px
    box-sizing: border-box
    padding: 50px
    .show-color
      width: 200px
      margin-bottom: 50px
    .father-wrapper
      .father
        list-style: none
        cursor: pointer
        color: #818080
        margin: 10px
        display: flex
        justify-content: center
        flex-direction: column
        align-items: center
        text-align: left
        &:hover
          color: #242424
        &.active
          color: #242424
        .title
          display: inline-block
          width: 200px
        .child-wrapper
          padding-left: 20%
          width: 100%
          .child
            cursor: pointer
            list-style: none
            .child-item
              display: inline-block
              color: #818080
              width: 100%
              text-align: left
              &:hover
                color: #242424
              &.active
                color: #E95A47
  .right
    background-color: #fff
    padding-left: 370px
    min-height: 100vh
    .top
      display: flex
      position: fixed
      width: 100%
      z-index: 50
      padding: 45px 0 45px 50px
      background-color: #fff
      top: 80px
      .title
        font-size: 30px
    .content
      padding-top: 100px
      text-align: center
      display: flex
      justify-content: center
      flex-direction: column
      align-items: center
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis
      .title
        width: 150px
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
      .el-image
        display: block
        margin-bottom: 10px
        width: 120px
        height: 120px
      .desc
        color: #818080
        font-size: 14px
@media screen and (max-width: 767px)
  .top
    padding: 25px 0 25px 50px !important
  .right
    padding: 0 !important
</style>
